  const [editingTask, setEditingTask] = useState(null);
  const [editingItem, setEditingItem] = useState(null);
  const [editingAssembly, setEditingAssembly] = useState(null);
  const [editingCatalogItem, setEditingCatalogItem] = useState(null);
  const [expandedSTL, setExpandedSTL] = useState(null); // { stlData, name, x, y }   
  
  const [newWarehouse, setNewWarehouse] = useState({ name: '', quantity: '', items: [] });
  const [newTask, setNewTask] = useState({ title: '', assignedTo: '' });
  const [newItem, setNewItem] = useState({ name: '', quantity: '', unit: '' });
  const [isCustomItem, setIsCustomItem] = useState(false);
  const [newAssembly, setNewAssembly] = useState({ name: '', components: [], instructions: '' });
  const [newCatalogItem, setNewCatalogItem] = useState({ name: '', description: '', quantity: '', unit: '', photo: '', stlFile: '' });

  //  Telegram WebApp
  const [isTelegram, setIsTelegram] = useState(false);
  const [tgTheme, setTgTheme] = useState(null);
  const [tgColors, setTgColors] = useState(null);
  const [isDarkTheme, setIsDarkTheme] = useState(false);
  
  //      UI
  const [debugLogs, setDebugLogs] = useState([]);
  const [showDebugPanel, setShowDebugPanel] = useState(false);

  // URL API  (HTTPS  Cloudflare Tunnel)
  const API_URL = 'https://vocabulary-decent-writings-rebound.trycloudflare.com/api';
  
  //    
